import './App.css';
import React from "react";
import axios from 'axios'
import QRCode from 'qrcode.react'
import Header from "./Header";

class AppNew2Eth extends React.Component{

  constructor(props) {
    super(props);
    this.state = {
      address: '',
      isShowQrCode: false,
      newAddress: ''
    }
  }

  async getNewReceiptAddress() {
    if (window.ethereum) {
      window.web3 = new Web3(ethereum);

      try { //log in
        await ethereum.enable();
        var address = web3.eth.defaultAccount || window.ethereum.address;
        let url = `https://rpc1.newchain.newtonproject.org/newbridge/account?ethereum_recipient_address=${address}&direction=new2eth`
        axios({
          method: 'get',
          url: url,
        }).then((response) => {
          console.log(response.data.newchain_deposit_address);
          if(response.status === 200) {
            this.setState({
              newAddress: response.data.newchain_deposit_address,
              isShowQrCode: true
            });
          }
        }).catch((error) => {
          console.log(error)
        })
      } catch (err) { //user denied
        console.log("ERROR: login denied");
        return; //quit
      }
    } else {
      console.log("ERROR: metamask not found");
      return; //quit
    }


  }

  handleInputChange(e) {
    this.setState(
      {
        address: e.target.value
      }
    )
  }

  render() {
    return(
      <div className="Col">
      <Header title="New2Eth"/>
        <div className="Col-margin center">
          <input className="big-margin" placeholder="请输入ETH接受地址" onChange={this.handleInputChange.bind(this)} defaultValue="0x13e96a46a08ae692a4633472e6ab3cd68a91feb6"/>
            <button className="big-margin" onClick={this.getNewReceiptAddress.bind(this)}>确认</button>
            <QRCode className="big-margin" value={this.state.newAddress} style={{ visibility: this.state.isShowQrCode ? 'visible' : 'hidden'}}/>
        </div>
      </div>
    )
  }
}


export default AppNew2Eth;
