import React from 'react';

class Card extends React.Component{

  constructor(props) {
    super(props);
  }

  render(){
    return (
      <a href={this.props.target}>
      <div style={{ height: '200px', background: '#f00', margin: '20px'}}
      >
        <p>{this.props.name}</p>
      </div>
      </a>
    )
  }
}
export default Card