import React from "react";
import back from './asset/back_black.png'

class Header extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    return(
      <div className="header">
        <image src={back}/>
        <p className="title">{this.props.title}</p>
      </div>
    )
  }
}
export default Header